.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbarDropdownMenu {
  width: 800px;
}

.sideMenuButton {
  width: 100%;
}

.scoresCarousel {
  background-color: #444;
}

.scoresCarouselItem {
  background-color: rgb(228, 113, 20);
  height: 75px;
  border-right: 1px solid #444;
}

.content {
  padding-top: 50px;
}

.teamCard {
  height: 175px;
  margin-top: 40px;
}

.teamCardHeader {
  height: 60%;
}

.teamCardLogo {
  overflow: hidden;
  height: 100%;
  width: 10%;
  position: relative;
}

.teamCardLogoImage {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-70%, -50%);
  width: 70%;
}
